@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.4);
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container {
  width: 100%;
  background-color: #007aff;
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  color: white;
  justify-content: center;
  align-items: center;
}

.body_container {
  width: 1400px;
  margin: 10px auto;
  display: grid;
}
.body_container_center {
  display: grid;
  width: 100%;
  grid-template-columns: auto auto auto;
  grid-column-gap: 50px;
  grid-row-gap: 10px;
  padding: 10px;
}
.charList {
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px;
  font-size: 20px;
}
.charList img:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  opacity: 0.6;
}

.charList_items {
  padding: 10px;
  display: flex;
  background-color: rgb(46, 46, 46, 0.2);
  margin: 10px;
}

.charList_img {
  width: 100%;
  height: auto;
  padding: 10px;
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
.body_container_fluid {
  position: relative;
}
.popup-container {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100vw;
  height: 100%;
  top: 0;
  z-index: 49;
  left: 0;
  display: flex;
  justify-content: center;
}
.popup-container button {
  z-index: 30;
  bottom: 30px;
  position: absolute;
  background-color: #f1f1f1;
  color: black;
  font-size: 16px;
  padding: 16px 30px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
  transition: 0.5s;
}
.popup-container button:hover {
  background-color: rgb(46, 46, 46);
  color: #f1f1f1;
}
.popup-detail {
  z-index: 55;
  position: fixed;
  width: 750px;
  height: 750px;
  background-color: #1f8bff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  margin: 10px;
}
.popup-detail span {
  padding: 10px;
  width: 350px;
  margin: 10px;
  background-color: aquamarine;
  border-radius: 3px;
  color: rgb(46, 46, 46);
}
.popup-detail span:hover {
  opacity: 0.8;
}
.popup-detail img {
  width: 400px;
  height: 400px;
  border-radius: 4px;
  -webkit-transform: none !important;
          transform: none !important;
  opacity: 1 !important;
}

.footer_container {
  width: 100%;
  background-color: #007aff;
  margin: 0 auto;
  padding: 20px 0 20px 0;
  color: white;
}

